.topbar {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 200;
  background: #222;
  color: #fff;
  padding: 0.5rem 0;
  /* Only vertical padding */
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* Add this */
  min-height: 3.5rem;
  border-bottom: 1px solid #111;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}

.back-arrow {
  font-size: 2.2rem;
  background: none;
  border: none;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  padding: 0;
  margin-left: 2rem;
  line-height: 1;
  height: 2.2rem;
  display: flex;
  align-items: center;
}

.home-button {
  font-size: 2.4rem;
  background: none;
  border: none;
  color: #ffffff;
  cursor: pointer;
  font-weight: bold;
  padding: 0;
  line-height: 1;
  display: flex;
  align-items: center;
  margin-right: 2rem;
}